import React, { Component } from "react";
import { Alert, Button, Badge } from 'reactstrap';
import { Link } from "react-router-dom";
import { Breadcrumb, BreadcrumbItem } from 'reactstrap';

import UnknownBanner from "assets/AL/UnknownBanner.png";

export default class ShipHeader extends Component {
  render() {
    const bannerData = this.props.bannerData;
    const names      = this.props.names;
    const rarity     = this.props.rarity;

    const shipName = names.en;
    const Codename = names.code;

    let bannerRarity  = null;
    let bannerStatus  = true;
    let defaultBanner = UnknownBanner;

    if(bannerData) {defaultBanner = bannerData.skins[0].banner; bannerStatus = false;};
    if(bannerStatus) {console.log(shipName)};

    if(rarity === "Normal")                              {bannerRarity = "rarity-1"}
    if(rarity === "Rare")                                {bannerRarity = "rarity-2"}
    if(rarity === "Elite")                               {bannerRarity = "rarity-3"}
    if(rarity === "Super Rare" || rarity === "Priority") {bannerRarity = "rarity-4"}
    if(rarity === "Ultra Rare" || rarity === "Decisive") {bannerRarity = "rarity-5"}
    if(bannerRarity === null)                            {bannerRarity = "rarity-0"}
    return (
      <>
        <Breadcrumb>
          <BreadcrumbItem><Link to={`/home`}>Home</Link></BreadcrumbItem>
          <BreadcrumbItem><Link to={`/ship`}>Ship</Link></BreadcrumbItem>
          <BreadcrumbItem active>{shipName}</BreadcrumbItem>
        </Breadcrumb>
        <Alert color="warning">
          This page is under Development!
        </Alert>
        <div>
          <div className={bannerRarity + " dis-inlineblock"}>
            <img src={defaultBanner} referrerPolicy="no-referrer" alt="..." className="img-fluid" />
          </div>
          <h1>{shipName}</h1>
          <h6>{Codename}</h6>
          <Button color="primary" size="sm" block disabled>Skin List<Badge color="info">Comming Soon!</Badge></Button>
        </div>
      </>
    )
  }
}